.headerline {
    display: flex;
    justify-content: space-between;
    padding: 5px 15px;
    margin-bottom: 20px;
}
.input {
    padding: 5px 10px;
    height: 20px;
}
.items {
    display: flex;
    flex-wrap: wrap;
}
.item {
    display: flex;
    width: calc(25% - 35px);
    padding: 5px 10px 15px 25px;
}
@media(max-width:1199px) {
    .item {
        display: flex;
        width: calc(33% - 25px);
        padding: 5px 10px 10px 15px;
    }
}
@media(max-width:919px) {
    .item {
        display: flex;
        width: calc(50% - 25px);
        padding: 5px 10px 10px 15px;
    }
}

.avatar {

    width: 60px;
    height: 60px;
    border-radius: 5px;
    overflow:hidden;
}
.img {
    width: 100%;
}
.about {
    margin-left: 15px;
}
.name {
    font-size: 14px;
    font-weight: 500;
}
.name a {
    text-decoration: none;
    color: rgb(3, 121, 194);
}
.location {
    font-size: 12px;
}
.reputation {
    font-size: 12px;
    font-weight: 600;
}

.tags {
    display: flex;
    flex-direction: column;
}
.tags a {
    text-decoration: none;
} 
.tag {
    padding: 5px 8px;
    background: rgb(214, 232, 255);
    color: rgb(3, 121, 194);
    border-radius: 5px;
    margin-right: 5px;
    font-size: 12px;
    margin: 5px;
}
.line {
    border-bottom: 1px solid lightgray;
    padding: 10px;
}