.user {
    display: flex;
    
}
.conteiner {
    display: flex;
    border-radius: 4px;
    height: 65px; 
}
.asked {
    display: flex;
    font-size: 12px;
    color: gray;
    margin: 5px;
}
.about {
    display: flex;
    justify-content: center;
    margin-right: 20px;
    margin-top: 3px;
}
.icon {
    padding-top: 3px;
}
.icon img {
    width: 20px;
    height: 20px;
    border-radius: 2px;
    margin: 0 5px 0 5px;
}
.name {
    display: flex;
    padding-left: 5px;
    padding-top: 3px;
}
.user_name {
    color: rgb(3, 121, 194);
    font-size: 13px;
    font-weight: 500;
    margin-bottom: 3px;
}
.reputation {
    font-size: 12px;
    font-weight: 700;
    color: rgb(88, 88, 88);
    margin-left: 15px;
    padding-bottom: 5px;
}
