.container {
    display: flex;
    align-items: flex-end;
    flex-wrap: nowrap;
    width: 200px;
}
.icon {
    display: flex;
}
.icon img {
    border-radius: 2px;
    width: 25px;
    height: 25px;
}
.reputation {
    display: flex;
    margin: 3px 10px;
    font-size: 14px;
    font-weight: 600;
}
.badge {
    display: flex;
}
.gold {
    display: flex;
    margin: 3px 5px;
    font-size: 14px;
    font-weight: 600;
    color: gold;
}
.silver {
    display: flex;
    margin: 3px 5px;
    font-size: 14px;
    font-weight: 600;
    color: silver;
}
.bronze {
    display: flex;
    margin: 3px 5px;
    font-size: 14px;
    font-weight: 600;
    color: burlywood;
}